@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
}

.landgb {
	height: 100vh;
	width: 100vw;
	background: linear-gradient(180deg, #f7f0f7 20.71%, #bbd9e9 100%);
}

.ins {
	position: relative;
	top: 15.6%;
	left: 9%;
	display: inline-block;
}
.ins .title {
	font-family: Merriweather;
	font-style: normal;
	font-weight: normal;
	font-size: 10vw;
	margin: 0;
	color: #000000;
}
.tag {
	font-family: Merriweather;
	font-style: normal;
	font-weight: normal;
	font-size: 2vw;
	margin: 0;
}
.signin {
	width: 30%;
	cursor: pointer;
}
.image {
	position: absolute;
	width: 50%;
	right: 5%;
	bottom: 5%;
}

.side {
	flex: 0.33 1;
	background: linear-gradient(180deg, #f7f0f7 20.71%, #bbd9e9 100%);
	display: flex;
	justify-content: center;
	align-items: center;
}
.boxinside {
	width: 80%;
	height: 80%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}
.header {
	display: flex;
	height: 10%;
	justify-content: space-around;
	align-items: center;
	min-width: 70%;
}
.header img {
	border-radius: 50%;
}
.name {
	font-family: 'Roboto';
	font-style: normal;
	font-weight: bold;
	color: #6e6e6e;
	font-size: 1.5vw;
	margin: 0;
}
.stud {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	font-size: 1vw;
	margin: 0;
	color: #a9a9a9;
}
.btn {
	cursor: pointer;
	transition: all 0.5s ease;
}
.btn:hover {
	transform: scale(1.2) translateY(-10%);
	transition: all 0.5s ease;
}

.home {
	width: 100vw;
	height: 100vh;
	display: flex;
}
.dia {
	width: 80%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: auto;
}
.plus {
}

.com {
	width: 80%;
	height: 80%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}
.ddd {
	display: flex;
	height: 100vh;
	flex: 0.67 1;
	align-items: center;
	justify-content: center;
}
.head {
	width: 80%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.mc {
	font-family: 'Roboto';
	font-style: bold;
	font-weight: 500;
	font-size: 1.5vw;
	margin: 0;
}
.nc {
	margin: 0;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	font-size: 1vw;
	cursor: pointer;
	color: #e44579;
	transition: all 0.5s ease;
}
.nc:hover {
	transform: scale(1.2) translateY(-10%);
	transition: all 0.5s ease;
}
.classes {
	height: 80%;
	width: 80%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
	overflow: scroll;
	overflow-x: hidden;
}
.classes::-webkit-scrollbar {
	width: 8px;
}

.classes::-webkit-scrollbar-track {
	/* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
	background: linear-gradient(180deg, #f7f0f7 20.71%, #bbd9e9 100%);

	border-radius: 6px;
}

.classes::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 255, 255, 0.692);
}
.boxx {
	width: 20%;
	height: 25%;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #c9ddf5;
	border-radius: 15px;
	cursor: pointer;
	margin: 2%;
}

.class {
	width: 100vw;
	height: 100vh;
	display: flex;
}
.sidethingy {
	display: flex;
	flex-direction: column;
	height: 100vh;
	flex: 0.67 1;
	align-items: center;
	justify-content: center;
}
.Head {
	width: 100%;
	height: 20%;
	display: flex;
	align-items: flex-end;
	text-align: left;
}
.bod {
	width: 100%;
	height: 80%;
}
.Head p {
	align-items: left;
	display: inline-block;
	text-transform: capitalize;
	font-family: Roboto;
	font-weight: bold;
	font-size: 3vw;

	color: #000000;
	bottom: 0;
	float: bottom;
	margin: 0;
	margin-left: 5%;
	margin-bottom: 2%;
}

#videoContainer {
	width: 10%;
}

#videoCanvas {
	position: absolute;
}

#video {
}


.sdsds {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	overflow: auto;
	height: 100%;
}
.buttons {
	width: 60%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	margin: auto;
}
.data {
	width: 80%;
	height: 100%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
}
.boix {
	width: 20%;
	height: 20%;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #c9ddf5;
	border-radius: 15px;
	flex-direction: column;
}

